.transition-graph{
    display: flex;
    position: relative;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    margin-right: 40px;
    max-width: 50%;
    height: 100%;
}

.transition-graph__tooltip{
    background-color: white;
    /* position: absolute;
    top: 0;
    left: 0; */
    padding: 7px 5px;
    text-align: center;
    font-size: 14px;
    font-weight: bold;
    border-radius: 10px;
    color:#001F3D;
    width: 120px;
}

.transition-graph__graphics{
    display: inline-block;
    position: relative;
    width: 700px;
    height: 300px;
    resize: both;
    object-fit: cover;
    font-size: 50px;
    /* margin-top: 20px; */
}

.transition-graph__graphics_inside{
    position:absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    /* overflow: hidden; */
    object-fit: fill;
}

.transition-graph__title{
    color: white;
    margin-bottom: 0;
    font-weight: bold;
    font-size: 20px;
    margin-top: 15px;
    white-space: pre-wrap;
}

.transition-graph__buttons{
    display: flex;
    flex-direction: row;
    justify-content: flex-start;
    width: 100%;
    background-color: inherit;
    /* width: 60%; */
}

.transition-graph__button{
    border: none;
    cursor: pointer;
    font-family: inherit;
    font-weight: bold;
    border-radius: 30px;
    font-size: 14px;
    margin: 15px 10px 0;
    width: 80px;
    height: 25px;
}

.transition-graph__button_inactive{
    font-weight: bold;
    color: white;
    border: white 2px solid;
    background-color: #001F3D;
}

.transition-graph__button_active{
    background-color: #646060;
    color: white;
}

.transition-graph__legends{
    display: flex;
    flex-direction: row;
    text-align: center;
    justify-content: space-between;
    list-style: none;
    width: 95%;
    padding:0;
    padding-top: 10px;
    border-top: 1px solid white;
}

.transition-graph__legend{
    position: relative;
    font-size: 14px;
    line-height: 20px;
    font-weight: bold;
    width: 30%;
}

.transition-graph__legend_n0{
    color: #5ae316;
}

/* .transition-graph__legend_0{
    color: #679004;
} */

.transition-graph__legend_ans{
    color: white;
}

.transition-graph__legend_spc{
    color: yellow;
}

.transition-graph__line {
    fill: none;
    /* stroke: white; */
    stroke-width: 1;
}

.transition-graph__axis{
    color: white;
    font-family: inherit;
    font-size: 14px;
}

.transition-graph__source{
    width: 700px;
    text-align: start;
    color: white;
    font-size: 12px;
    font-style: italic;
    padding-left: 50px;
}

.transition-graph__grid{
    color: white;
    font-family: inherit;
    font-size: 14px;
}

.transition-graph__legend-button{
    position: relative;
    text-decoration: none;
    background-color: #001F3D;
    border-radius: 90px;
    width: 20px;
    height: 20px;
    font-weight: bold;
    cursor: pointer;
    text-align: center;
}

.transition-graph__legend-button_n0{
    color: #5ae316;
    border: 2px #5ae316 solid;
}

.transition-graph__legend-button_ans{
    color: white;
    border: 2px white solid;
}

.transition-graph__legend-button_spc{
    color: yellow;
    border: 2px yellow solid;
}

.transition-graph__background{
    position: absolute;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    top: -25px;
    left:-48vw;
    background-color: rgba(0,0,0,0.95);
    width: 47vw;
    height: 200%;
    z-index: 2;
    cursor: pointer;    
}

.transition-graph__legends-tooltip{
    position: relative;
    /* background-color: #fff; */
    padding: 10% 5%;
    /* top: 50px; */
    /* right: 30vw; */
    /* border-radius: 30px; */
    min-height: 70px;
    width: 90%;
    text-align: start;
    z-index: 3;
    /* color:white; */
    overflow-y: scroll;
    scroll-snap-type: y mandatory;
    -ms-verrflow-style: none;
    overflow: - moz-scrollbars-none;
    /* box-shadow: 0px 0px 80px #f3c9ff2e; */
    scrollbar-width: none;
    -ms-overflow-style: none; 
    transition:all 1s ease;
}

.transition-graph__legends-tooltip_hidden{
    display: none;
}

.transition-graph__legends-title{
    font-size: 26px;
    margin-top: 0;
}

.transition-graph__legends-paragraph{
    margin-top: 40px;
    font-size: 16px;
    line-height: 24px;
    white-space: pre-wrap;
    color:white;
}

