@media screen and (max-width: 1300px) {
    .transition-graph{
        margin-right: 0;    
    }

    .transition-graph__graphics{
        width: 500px;
        font-size: 40px;
    }
    
    .transition-graph__source{
        width: 500px;
    }

    .transition-graph__legends{
        width: 100%;
    }

    
.transition-graph__background{
    left:-50vw;
    width: 50vw;
    }
}

@media screen and (max-width: 1100px) {
    .transition-graph__background{
        width: 49vw;
        }
    }
@media screen and (max-width: 1000px) {
    .transition-graph{
        /* border-top: 1px solid white; */
        /* padding-top: 20px; */
        margin-right: 0;
        max-width: 100%;
        font-size: 30px;
        max-width: 100%;
    }

    .transition-graph__graphics{
        width: 500px;
        height: 300px;
        resize: both;
    }
    
    .transition-graph__buttons{
        justify-content: flex-start;
        width: 80%;
    }

    .transition-graph__button{
        margin: 15px 15px 15px 0;
    }

    .transition-graph__legends{
        padding: 0;
        padding-top: 20px;
        margin: 0 auto;
    }

    .transition-graph__source{
        width: 400px;
    }
    .transition-graph__background{
        left: -40vw;
        width: 40vw;
        }
}

@media screen and (max-width: 900px) {
    .transition-graph__graphics{
        width: 700px;
        height: 300px;
        resize: both;
    }

    .transition-graph__buttons{
        width: 100%;
    }

    .transition-graph__source{
        width: 700px;
    }
    .transition-graph__background{
        position: relative;
        background-color: inherit;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%; 
        border-left: 2px white solid;
        /* border-top: 1px white solid; */
    }

    .transition-graph__legends-tooltip{
        position: relative;
        padding: 0 5%;
        height: 200px;
        width: 90%;
        text-align: start;
    }

    .transition-graph__legends-title{
        margin: 0;
        margin-top: 20px;
        font-size: 16px;
    }

    .transition-graph__legends-paragraph{
        font-size: 14px;
    }

    /* .transition-graph__legend-button{
        width: 18px;
        height: 18px;
    }    */
}

@media screen and (max-width: 750px) {
    .transition-graph__graphics{
        width: 500px;
        height: 300px;
        resize: both;
    }
    
    .transition-graph__source{
        width: 100%;
        text-align: center;
        padding-left: 0;
    }
}

@media screen and (max-width: 500px) {
    .transition-graph__graphics{
        width: 400px;
    }
    .transition-graph__legends{
        flex-direction: column;
    }
    .transition-graph__legend{
        display: list-item;
        list-style-type: square;
        list-style-position: inside;
        width: 100%;
        text-align: start;
    }
}

@media screen and (max-width: 420px) {
    .transition-graph__graphics{
        width: 350px;
    }
    .transition-graph__buttons{
        width: 100%;
    }
}

@media screen and (max-width: 320px) {
    .transition-graph__graphics{
        width: 300px;
    }
}